export function OnlineDate(timeR) {
  var time = new Date(timeR);

  var hours = time.getHours();
  var minutes = time.getMinutes();

  if (minutes.toString().length === 1) {
    minutes = "0" + minutes;
  }
  if (hours.toString().length === 1) {
    hours = "0" + hours;
  }

  var diff = (new Date().getTime() - time.getTime()) / 1000,
    day_diff = new Date().getDay() - time.getDay();
  var month_diff = new Date().getMonth() + 1 - (time.getMonth() + 1);
  var year = time.getFullYear(),
    month = time.getMonth() + 1,
    day = time.getDate();

  if (isNaN(day_diff) || day_diff < 0 || month_diff >= 1)
    return (
      year.toString() +
      "." +
      (month < 10 ? "0" + month.toString() : month.toString()) +
      "." +
      (day < 10
        ? "0" + day.toString() + " in " + hours + ":" + minutes
        : day.toString() + " in " + hours + ":" + minutes)
    );

  var r =
    (day_diff === 0 &&
      ((diff < 60 && "Ð¢Ð¾Ð»ÑÐºÐ¾ ÑÑÐ¾") ||
        (diff < 120 && "1 Ð¼Ð¸Ð½ÑÑÑ Ð½Ð°Ð·Ð°Ð´") ||
        (diff < 3600 && Math.floor(diff / 60) + "Ð¼Ð¸Ð½ÑÑ. Ð½Ð°Ð·Ð°Ð´") ||
        (diff < 7200 && "1 ÑÐ°Ñ Ð½Ð°Ð·Ð°Ð´") ||
        (diff < 86400 && Math.floor(diff / 3600) + "ÑÐ°ÑÐ¾Ð² Ð½Ð°Ð·Ð°Ð´"))) ||
    (day_diff === 1 && "ÐÑÐµÑÐ° Ð²" + hours + ":" + minutes) ||
    (day_diff < 7 && day_diff + "Ð´Ð½. Ð½Ð°Ð·Ð°Ð´ Ð²" + hours + ":" + minutes) ||
    (day_diff < 31 &&
      Math.ceil(day_diff / 7) + "Ð½ÐµÐ´ÐµÐ». Ð²" + hours + ":" + minutes);
  return r;
}
export function prettyDate(num) {
  /**
   * ÐÐ¿ÑÐµÐ´ÐµÐ»ÑÐµÑ Ð¿ÑÐ°Ð²Ð¸Ð»ÑÐ½Ð¾Ðµ Ñ ÑÐ¾ÑÐºÐ¸ Ð·ÑÐµÐ½Ð¸Ñ ÑÑÑÑÐºÐ¾Ð³Ð¾ ÑÐ·ÑÐºÐ° Ð¾ÐºÐ¾Ð½ÑÐ°Ð½Ð¸Ðµ Ð¼ÐµÑÑÑÐ°
   * @param int $num
   * @param array $postfixes
   * @return string
   */
  function postfix(num, postfixes) {
    //ÐÐµÐ»Ð¸Ð¼ ÑÐ¸ÑÐ»Ð¾ Ð±ÐµÐ· Ð¾ÑÑÐ°ÑÐºÐ° Ð½Ð° 100
    num = num % 100;

    //ÐÑÐ»Ð¸ Ð±Ð¾Ð»ÑÑÐµ 19, Ð´ÐµÐ»Ð¸Ð¼ ÐµÐ³Ð¾ Ð±ÐµÐ· Ð¾ÑÑÐ°ÑÐºÐ° ÐµÑÑ ÑÐ°Ð·, ÑÐ¶Ðµ Ð½Ð° 10
    if (num > 19) {
      num = num % 10;
    }

    //Ð Ð·Ð°Ð²Ð¸ÑÐ¸Ð¼Ð¾ÑÑÐ¸ Ð¾Ñ ÑÐ¾Ð³Ð¾, ÐºÐ°ÐºÐ¸Ðµ ÑÐ¸ÑÐ»Ð° Ð¾ÑÑÐ°Ð»Ð¸ÑÑ, Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ñ
    switch (num) {
      case 1:
        return postfixes[0];

      case 2:
      case 3:
      case 4:
        return postfixes[1];

      default:
        return postfixes[2];
    }
  }

  //ÐÐ¿ÑÐµÐ´ÐµÐ»ÑÐµÐ¼ Ð¼Ð°ÑÑÐ¸Ð²Ñ Ñ Ð³Ð¾Ð´Ð°Ð¼Ð¸ Ð¸ Ð¼ÐµÑÑÑÐ°Ð¼Ð¸
  var yearsPostfixes = ["Ð³Ð¾Ð´", "Ð³Ð¾Ð´Ð°", "Ð»ÐµÑ"],
    monthesPostfixes = ["Ð¼ÐµÑÑÑ", "Ð¼ÐµÑÑÑÐ°", "Ð¼ÐµÑÑÑÐµÐ²"];

  //ÐÐµÐ»Ð¸Ð¼ Ð½Ð°ÑÐ°Ð»ÑÐ½Ð¾Ðµ ÑÐ¸ÑÐ»Ð¾ Ð±ÐµÐ· Ð¾ÑÑÐ°ÑÐºÐ° Ð½Ð° 12, Ð¿Ð¾Ð»ÑÑÐ°ÐµÐ¼ ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾ Ð¼ÐµÑÑÑÐµÐ²
  var monthes = num % 12,
    //ÐÑÐ½Ð¸Ð¼Ð°ÐµÐ¼ Ð¸Ð· Ð½Ð°ÑÐ°Ð»ÑÐ½Ð¾Ð³Ð¾ ÑÐ¸ÑÐ»Ð° ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾ Ð¼ÐµÑÑÑÐµÐ² Ð¸ Ð´ÐµÐ»Ð¸Ð¼ ÑÑÐ¾ Ð½Ð° 12,
    //Ð¿Ð¾Ð»ÑÑÐ°ÐµÐ¼ ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾ Ð»ÐµÑ
    years = (num - monthes) / 12;

  //ÐÐ¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ ÑÐµÐ·ÑÐ»ÑÑÐ°Ñ postfix() Ð·Ð°Ð²Ð¸ÑÐ¸Ð¼Ð¾ÑÑÐ¸ Ð¾Ñ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ð¹ $years Ð¸ $monthes
  if (years === 0) {
    return monthes + " " + postfix(monthes, monthesPostfixes);
  }

  if (monthes === 0) {
    return years + " " + postfix(years, yearsPostfixes);
  }

  return (
    years +
    " " +
    postfix(years, yearsPostfixes) +
    " " +
    monthes +
    " " +
    postfix(monthes, monthesPostfixes)
  );
}
